import React from "react";
import "./component-Styles/projectCard.css";

import {  FcCalendar } from "react-icons/fc";
import {FiExternalLink} from "react-icons/fi"
import { FaEye } from "react-icons/fa";
import { NavLink } from "react-bootstrap";

const ProjectCard = ({
  title,
  projectImg,
  link,
  views,
  tech,
  date,
  tech1,
  tech2,
  tech3,
  tech4,
  tech5,
}) => {
  return (
    <div>
      {" "}
      <div className="projectcard  rounded-4 ">
        <div className="row ">
          <div className="col-sm-8 col-md-8 col-lg-7  overflow-hidden">
            <img
            height={120}
          
              src={projectImg}
              className="rounded-4 projectimg p-2 "
              alt="E-Commerce Web-App"
            />
          </div>
          <div className="  d-none   d-sm-block  col-lg-4 col-md-4 col-xl-5">
            <div
              className="techstack px-2 pt-1 rounded-5 rounded-bottom-2"
              style={{
                backgroundImage: " linear-gradient(to right, #870000, #190a05)",
                color: "whitesmoke",
              }}
            >
              {" "}
              <h6 className="text-uppercase pt-1 text-center">
                tech Stack
              </h6>{" "}
              <hr />
              <div className="text-center  d-flex flex-column gap-2  fw-semibold text-capitalize" style={{fontSize:"10px"}}>
                <span>{tech1}</span>
                <span>{tech2}</span>
                <span>{tech3} </span>
                <span>{tech4}</span>
                <span>{tech5}</span>
              </div>
            </div>
          </div>
        </div>
    

        <div>
          <div className=" fw-bold d-flex flex-row flex-wrap gap-2 pt-1 rounded-3  justify-content-between mx-3">
            <div className="  d-flex flex-row flex-wrap gap-2">
              {" "}
              <a
                href={link}
                target="_blank"
                className="    text-reset    "
              >
                <FiExternalLink className="fs-6" title="visit page" /> 
              </a>
            </div>
            <div className="  overflow-hidden">
              {" "}
              <p className="  mt-1 ">{title}</p>
            </div>
            <div className="  fw-semibold mt-1 ">
              <FcCalendar className="fs-5  me-1  " /> {date}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProjectCard;
